<template>
  <div class="notice-detail-page">
    <navigation-bar  @onLeftClick="onBack" :pageName="reqData.systemName + '消息'">
    </navigation-bar>

    <div class="notice-content"  v-show="!isNoNetwork">
      <div class="notice-title">{{reqData.messageTopic}}</div>
      <div class="notice-time">{{reqData.sendTime}}</div>
      <div class="contentArea">{{reqData.sendMessage}}</div>
    </div>
    <div class="no-networking" v-show="isNoNetwork">
      <h3>网络请求失败</h3>
      <p>请检查您的网络</p>
      <div @click="setNoticeRead">重新加载</div>
    </div>
  </div>
</template>

<script>

  import {
    requestMsgReadedById
  } from "../../services/noticeService";
  import NavigationBar from '../title/NavigationBar';

  import Vue from 'vue';
  import {Toast} from 'vant';
  Vue.use(Toast);

  export default {
    name: "NoticeDetail",
    components: {
      NavigationBar
    },
    mounted () {
      // 获取传参
      let routerParams = this.$route.params;
      if (routerParams != undefined
      && routerParams.noticeReq != undefined) {
          this.reqData = routerParams.noticeReq;
      }
      this.setNoticeRead();
    },
    data() {
      return {
        active: 0,
        title: '物业通知',
        reqData:{},
        //通知内容
        noticeDetail: {},
        isNoNetwork: false,
      };
    },

    methods: {
      onBack() {
        this.$router.back(-1);
      },
      // 将对应的消息设置为已读
      setNoticeRead() {
        if (this.reqData.isRead != 1) {
          requestMsgReadedById({id: this.reqData.messageId})
           .then(noteData => {
             this.isNoNetwork = false;
             if (noteData.resultCode == 1) {
               // 设置已读失败
               console.log("设置消息已读失败.");
             }
           })
           .catch(error => {
             // this.isNoNetwork = true;
             Toast(error);
           });
        } else {
           console.log("message already read, need not set read " + this.reqData.isRead);
        }
      }
    }
  };
</script>

